#main {
    text-align: center;
    padding: 64px 144px 64px;
    font-family: 'Inter', sans-serif;
}
#profile_img {
    width: 88px;
    height: 88px;
    margin-bottom: 24px;
    border-radius: 50%;
}
.profile-pic {
    position: relative;
    width: 88px;
    height: 88px;
    margin: auto;
}
.profile-pic:focus {
    border: 5.5px solid #EBE9FE;
}
#profile-hover {
    background-color: transparent;
    position: absolute;
    left: 0;
    width: 88px;
    height: 88px;
    border-radius: 50%;
    transition: 0.5s;
}
.profile-pic:hover #profile-hover{
    background: linear-gradient(0deg, rgba(52, 64, 84, 0.65), rgba(52, 64, 84, 0.65));
}
.profile-pic:hover #camera{
    display: block; 
}    
#camera{
    transition: 0.5s;
    position: absolute;
    bottom: 8px;
    right: 32px;
    display: none;
}
#twitter a{
    text-decoration: none;
    display: block;
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    margin: 24px 0 56px;
    color: #101828;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
}
#slack {
    display: none;
}
#links {
    display: flex;
    flex-direction: column;
    grid-gap: 24px;
    gap: 24px;
    align-items: center;
    -webkit-filter: drop-shadow(0px 1px 2px rgba(16, 24, 40, 0.05));
            filter: drop-shadow(0px 1px 2px rgba(16, 24, 40, 0.05));
}
.btn-links {
    width: 80%;
    padding: 20px 5px;
    text-decoration: none;
    background: #EAECF0;
    border: 1px solid #EAECF0;
    border-radius: 8px;
    width: 100%;
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    color: #101828;
}
.btn-links:hover {
    background: #D0D5DD;
}
.btn-links:focus {
    background: #EAECF0;
    border: 1px solid #98A2B3;
}
.social-section {
    margin: 24px 0 0;
    padding: 24px 0;
}
#social-slack {
    margin-right: 24px;
}
.shareBtn {
    position: absolute;
    top: 44px;
    right: 144px;
    padding: 10px;
    border-radius: 50%;
    border: 1px dashed #D0D5DD;
}
.shareBtn:hover {
    background: #F9FAFB;
}
#phone {
    padding: 15px 10px;
}
#share-hover-container {
    position: absolute;
    top: 0;
    top: 55px;
    right: 195px;
    height: 0;
    display: none;
}
.shareBtn:hover #share-hover-container {
    display: block;
}
.share-hover {
    padding: 8px 12px;
    width: 85px;
    height: 34px;
    background: #101828;
    border-radius: 8px;

    font-weight: 600;
    font-size: 12px;
    text-align: center;
    color: #FFFFFF;
    position: relative;
}
.share-hover::after {
    content: "";
    width: 10px;
    height: 10px;
    -webkit-transform: rotateZ(45deg);
            transform: rotateZ(45deg);
    top: 10px;
    right: -5px;
    background: #101828;
    position: absolute;
}
#phone {
    display: none;
}
footer {
    margin: 64px 0 0;
    padding: 48px 0 0;
}
footer div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    padding: 32px 0 0;
    border-top: 1px solid #EAECF0;
}
#zuri {
    width: 187.59px;
    height: 32px;
}
.footer-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #667085;
}
#ig {
    width: 132.13px;
}


@media screen and (max-width: 850px) {
    #main {
        padding: 64px 80px 64px;    
    } 
    .shareBtn {
        position: absolute;
        top: 44px;
        right: 80px;
    }
    #share-hover-container {
        position: absolute;
        top: 0;
        top: 55px;
        right: 130px;
        height: 0;
    }
    #phone {
        display: block;
    }
    #pc {
        display: none;
    }
    footer div{
        display: block;
        padding: 32px 0 0;
    }
    #zuri {
        width: 140.69px;
        height: 20px;
    }
    .footer-text {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #667085;
    }
    #ig {
        width: 99.1px;
    }
}

@media screen and (max-width: 600px) {
    #main {
        padding: 64px 16px 32px;
    } 
    #twitter {
        font-size: 18px;
        line-height: 28px;
        margin: 24px 0 32px;
    }
    .btn-links {
        font-size: 14px;
        line-height: 20px;
    }
    .shareBtn {
        position: absolute;
        top: 44px;
        right: 16px;
    }
    #share-hover-container {
        position: absolute;
        top: 0;
        top: 55px;
        right: 65px;
        height: 0;
    }
    footer {
        margin: 32px 0 0;
        padding: 32px 0 0;
    }
    footer div {
        display: block;
        padding: 32px 0 0;
    }
    #zuri {
        width: 140.69px;
        height: 20px;
    }
    .footer-text {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #667085;
    }
    #ig {
        width: 99.1px;
    }
}
#contact-main {
    padding: 92px 10%;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
}
.main-container {
    margin: auto;
    width: 720px;
}
h1 {
    font-weight: 600;
    font-size: 36px;
    line-height: 44px;
    letter-spacing: -0.02em;
    color: #101828;
    margin: 0 0 20px;
}
h4 {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    margin: 0 0 48px;
}
.label {
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: #344054;
    margin: 0 0 6px;
}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #667085;
}
input::placeholder, textarea::placeholder {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #667085;
}
.first {
    display: flex;
    justify-content: space-between;
}
.name-input {
    display: flex;
    flex-direction: column;
    flex-basis: 48%;
}
.name-input input{
    width: 100%;
}
.space {
    margin: 0 0 24px;
}
.input-pad {
    padding: 10px 0 10px 14px;
    border: 1px solid #D0D5DD;
    border-radius: 8px;
    font-size: 16px;
}
.input-pad:focus {
    outline: 1px solid #84CAFF;
}
#email {
    display: block;
    width: 100%;
}
#message {
    display: block;
    width: 100%;
}
.error {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #F83F23;
}
#message::-webkit-input-placeholder {
    font-size: 16px;
    line-height: 24px;
    font-family: 'Inter';
}
#message::placeholder {
    font-size: 16px;
    line-height: 24px;
    font-family: 'Inter';
}
#check {
    font-size: 16px;
    line-height: 24px;
    color: #475467;
    margin: 0 12px 32px 0;
}
#check:checked {
    background: #FFFFFF;
    
}
button {
    width: 100%;
    background: #1570EF;
    border: 1px solid #1570EF;
    padding: 12px 20px;
    border-radius: 8px;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
}
button:hover {
    background: #175CD3;
}
button:focus {
    background: #1570EF;
    border: 1px solid #1570EF;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05), 0px 0px 0px 4px #D1E9FF;
}
button:disabled {
    background: #B2DDFF;
    border: 1px solid #B2DDFF;
}


@media screen and (max-width: 850px){
    #contact-main {
        padding: 92px 5%;
    }
    .main-container {
        margin: auto;
        width: 600px;
    }
}
@media screen and (max-width: 600px){
    #contact-main {
        padding: 64px 0 0;
    }
    .main-container {
        margin: auto;
        width: 90%;
    }
    .first {
        display: block;
    }
    .name-input {
        margin: 0 0 24px 0;
    }
    .check-margin {
        margin: 0 0 32px 0;
    }
    #check {
        font-size: 16px;
        line-height: 24px;
        color: #475467;
        margin: 0 12px 0 0;
    }

    h4 {
        font-size: 18px;
    }
}

